// widgets spacing
$widget-bottom-margin: 8px;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 15px;

// list items color
$widget-list-item-background-color: #999999;

// list items border
$widget-list-item-border-color: #cccccc;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #ffffff;
$widget-heading-color: #999999;

// links
$widget-link-color: #ffffff;
$widget-link-hover-color: #a4a4a4;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;

footer {
    @import 'widgets/default-wordpress-widgets-flat';

    .qodef-grid-col-12 {
        text-align: center;

        .widget {
            margin-bottom: 8px;
        }
    }

    .widget {
        color: $widget-text-color;
        margin-bottom: 40px;

        a{
            color: $widget-text-color;
        }
        .textwidget {
            color: $widget-text-color;

            a{
                color: $widget-text-color;

                &:hover{
                    color: $widget-link-hover-color;
                }
            }
        }

        &.widget_nav_menu{
            ul#menu-footer-custom-menu {
                li {
                    display: inline-block;
                    padding: 15px 23px;
                    text-transform: uppercase;

                    a{
                        color: #fff;
                        font-weight: 500;

                        &:hover {
                            color: $widget-link-hover-color;
                        }
                    }
                }
            }
        }
    }
}