@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.qodef-comment-holder {
    @include qodefRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .qodef-comment-holder-inner {
        padding: 41px 0 20px;
    }
	
	.qodef-comment {
        border-bottom: 1px solid $default-border-color;
		
		&.qodef-pingback-comment {
			
			.qodef-comment-text {
				padding: 0;
			}
		}
	}

    .qodef-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include qodefRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 11px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .qodef-comments-title {
        margin-bottom: 28px;

        > * {
            margin: 0;
        }
    }

    .qodef-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 70px;
        height: 70px;
        float: left;

        img {
            display: block;
        }
    }

    .qodef-comment-text {
        padding: 0 0 35px 97px;
        min-height: 70px;

        .qodef-comment-info {
            @include qodefRelativeHolderLayout();
            margin: 6px 0 1px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            margin-right: 10px;
            font-size: 15px;
            color: $default-text-color;

            &:hover {
                color: $first-main-color;
            }
        }

        .comment-reply-link{
            display: inline-block;
            margin-top: 20px;

            &:after {
                content: '\e87a';
                position: relative;
                font-size: 15px;
                font-family: "Linearicons-Free";
                padding-left: 10px;
            }
        }

        .qodef-comment-name {
            float: left;
            margin: 0;
        }

        .qodef-comment-date {
            margin: 0;
            font-size: 15px;
        }

        .qodef-text-holder p {
            margin: 16px 0 7px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-text-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.qodef-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .qodef-comment-form-inner {
        padding: 43px 0 78px;
    }
    
    .comment-reply-title {
        margin: 0 0 37px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .qodef-comment-holder {
	    
        .qodef-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .qodef-comment-holder {
	    
        .qodef-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .qodef-comment-text {
	        
            .qodef-comment-date,
            .qodef-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .qodef-comment-holder {

        .qodef-comment-image {
            float: none;
        }

        .qodef-comment-text {
            padding: 20px 0 35px;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.qodef-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include qodefButtonDefaultStyle();
	@include qodefButtonMediumParams();
	@include qodefButtonSolidColor();
	cursor: pointer;
	
	&:hover {
		background-color: #fff;
        border-color: #000;
        color: #000;
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
    float: right;
}

#respond textarea,
.qodef-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 0;
}

.qodef-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}

.qodef-newsletter{
    .wpcf7-form-control.wpcf7-textarea{
        max-height: 235px;
        padding-top: 48px;
        margin-bottom: 2px;
    }

    input.wpcf7-form-control.wpcf7-submit{
        float: right;

        &:hover{
            color: $default-text-color;
            border-color: $default-text-color;
            background-color: #fff;
        }
    }
}

.qodef-newsletter-footer {
    .wpcf7-form-control.wpcf7-text {
        max-width: 310px;
        color: #fff;
    }
    input.wpcf7-form-control.wpcf7-submit {
        background-color: #fff;
        color: $default-text-color;
        padding: 5px 27px;
        margin-left: 9px;

        &:hover{
            color: #fff;
            background-color: $default-text-color;
            border-color: #fff;
        }
    }

    ~ .wpcf7-response-output {
        color: #fff;
    }
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.qodef-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.qodef-slider-is-moving {
		
		.owl-item {
			
			.qodef-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.qodef-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include qodefTransform(translateY($pos));
            }
        }
    }

    &.qodef-slider-hide-pagination .owl-dots  {
        display: none;
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-text-color;
			@include qodefTransform(translateY(-50%));
			@include qodefTransition(color .2s ease-out);
			
			&:hover {
				color: $first-main-color;
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 16px;
				line-height: 52px;
                text-align: center;
                width: 52px;
                height: 52px;
                background-color: #fff;
                font-weight: 600;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}

		.owl-prev {
			left: 0;
		}

		.owl-next {
			right: 0;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 21px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 2px;
				margin: 0 6px;
				background-color: $default-border-color;
				box-sizing: border-box;
				@include qodefTransition(all .2s ease-out);
			}
			
			&:hover,
			&.active {
				span {
                    width: 23px;
                    @include qodefTransition(all .2s ease-out);
                    background-color: $default-text-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .qodef-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .qodef-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;
                @include qodefTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.qodef-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .qodef-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .qodef-wrapper {
        padding: 0 !important;
    }
    
    .qodef-content {
	    height: 100vh;
    }
	
	&.admin-bar {
		
		.qodef-content {
			height: calc(100vh - 32px);
		}
	}

    .qodef-content-inner {
	    @include qodefTableLayout();
    }

    .qodef-page-not-found {
	    @include qodefTableCellLayout();
        padding: 0 20%;
	    text-align: center;

        .qodef-404-title {
            margin: 0;
        }
	
	    .qodef-404-subtitle {
            margin: 35px 0 0;
        }
	    
	    .qodef-404-text {
		    margin: 10px 0 0;
	    }

        .qodef-btn {
            margin: 30px 0 0;
	
	        &.qodef-btn-light-style {
		        color: #585858;
		        background-color: #fff;
		        border-color: #fff;
		
		        &:hover {
			        color: #fff !important;
			        background-color: transparent !important;
			        border-color: #fff !important;
		        }
	        }
        }
    }
	
	@include ipad-landscape {
		
		.qodef-content {
            height: auto;
            min-height: 100vh;
            margin-top: -70px!important;

            .qodef-content-inner {
                min-height: 100vh;
            }
		}
		
		&.admin-bar {
			
			.qodef-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}
	}

    @include ipad-portrait {

        .qodef-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#qodef-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include qodefTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include qodefTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 15px;
        text-align: center;
        color: #fff;
        background-color: $additional-dark-color;
        border: 1px solid $additional-dark-color;
        overflow: hidden;
        @include qodefTransition(all 0.2s ease-in-out);
        border-radius: 20px;

        i {
            @include qodefTransition(.15s ease .05s);
        }

        &:after {
            content: '';
            position: absolute;
            z-index: 1;
            top: 28px;
            left: 19px;
            height: 0;
            width: 0;
            opacity: 0;
            border: 0.4px solid $default-border-color;
            @include qodefTransition(0.2s );
        }

        &:hover {
            background-color: $default-text-color;
            border: 1px solid $default-text-color;
            height: 70px;
            line-height: 70px;
            border-radius: 20px;
            @include qodefTransition(0.2s );

            &:after {
                height: 16px;
                opacity: 1;
                @include qodefTransition(0.45s);
            }

            i {
                @include qodefTransform(translateY(-8px));
                @include qodefTransition(.2s );
            }

            span {
                margin-top: -5px;
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
        margin-top: -2px;
        margin-left: 0.3px;
        font-size: 12px;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

